"use client"
"use client";
import { useState } from "react";
import { useRouter } from "next/navigation";

export default function FiltreFormateur() {
  const [formation, setFormation] = useState("");
  const [dates, setDates] = useState("");
  const [tarifMax, setTarifMax] = useState("");
  const router = useRouter();

  const handleSearch = () => {
    const query = new URLSearchParams({
      formation,
      dates,
      tarifMax,
    }).toString();

    router.push(`/apps/rechercheFormation/tableAnime?${query}`);
  };

  return (
    <div className="min-w-full border border-gray-300 text-sm p-6 space-y-4 bg-white">
      <div className="grid grid-cols-1 sm:grid-cols-3 gap-4">
        <div>
          <label className="block mb-1 text-gray-700 font-medium">
            Formation recherchée
          </label>
          <input
            type="text"
            value={formation}
            onChange={(e) => setFormation(e.target.value)}
            placeholder="DevOps / code / client"
            className="w-full p-2 border border-gray-300 rounded-md focus:outline-none focus:ring-1 focus:ring-blue-500"
          />
        </div>

        <div>
          <label className="block mb-1 text-gray-700 font-medium">
            Dates souhaitées
          </label>
          <input
            type="text"
            value={dates}
            onChange={(e) => setDates(e.target.value)}
            placeholder="15-17/10/2024"
            className="w-full p-2 border border-gray-300 rounded-md focus:outline-none focus:ring-1 focus:ring-blue-500"
          />
        </div>

        <div>
          <label className="block mb-1 text-gray-700 font-medium">
            Tarif max/jour (€)
          </label>
          <input
            type="number"
            value={tarifMax}
            onChange={(e) => setTarifMax(e.target.value)}
            placeholder="700"
            className="w-full p-2 border border-gray-300 rounded-md focus:outline-none focus:ring-1 focus:ring-blue-500"
          />
        </div>
      </div>

      <div className="flex justify-end">
        <button
          onClick={handleSearch}
          className="px-6 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition"
        >
          Rechercher
        </button>
      </div>
    </div>
  );
}
